.about-us {
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__description {
    display: block;
  }

  &__title {
    // max-width: 392px;
    // margin-top: 0;
    margin-bottom: 10px;

    // font-family: $font--accent;
    // font-style: normal;
    text-align: start;
  }
  &__text {
    display: block;
    flex-direction: column;
    margin: 0;
    margin-bottom: 16px;
  }
  &__text :not(:first-child) {
    margin-bottom: 16px;
  }
}

@media screen and (max-width: 767.98px) {
  .about-us__container {
    flex-direction: column;
    align-items: center;
    padding-top: 5px;
  }
  .about-us__title {
    text-align: center;
    margin-bottom: 8px;
  }
  .about-us__text {
    font-size: 16px;
    line-height: 20px;
  }
  .extra-text {
    display: none;
  }
  .read-more {
    font-family: $font--accent;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    color: $color--main__dark-beige;
  }

  #read-more:checked ~ .extra-text {
    display: block;
  }

  .less {
    display: none;
  }

  #read-more:checked ~ .read-more .more {
    display: none;
  }

  #read-more:checked ~ .read-more .less {
    display: inline;
  }
}

#read-more:checked ~ .extra-text {
  display: block;
}

.less {
  display: none;
}

#read-more:checked ~ .read-more .more {
  display: none;
}

#read-more:checked ~ .read-more .less {
  display: inline;
}

@include tablet {
  .about-us__container {
    align-items: stretch;
    padding-top: 69px;
  }
  .about-us__title {
    margin-bottom: 16px;
  }
  .about-us__text {
    font-size: 16px;
    line-height: 20px;
    max-width: 336px;
  }
  .extra-text {
    display: none;
  }
  .read-more {
    font-family: $font--accent;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    color: $color--main__dark-beige;
  }

  #read-more:checked ~ .extra-text {
    display: block;
  }

  .less {
    display: none;
  }

  #read-more:checked ~ .read-more .more {
    display: none;
  }

  #read-more:checked ~ .read-more .less {
    display: inline;
    align-items: center;
  }
}

@include desktop-bg {
  .about-us__container {
    // position: relative;
    padding-top: 150px;
    // padding-left: 126px;
    // padding-right: 126px;
  }
  .about-us__text {
    max-width: 392px;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
  }
  // .about-us__title {
  //   font-weight: 400;
  //   font-size: 36px;
  //   line-height: 65px;
  // }
  .read-more {
    display: none;
  }
  .extra-text {
    display: inline-block;
  }
  .spoon {
    position: absolute;
    margin: 0;
    padding: 0;
    top: 550px;
    left: -76px;
    background: no-repeat url(../images/spoon/spoon@1x-min.png);
    width: 179px;
    height: 422px;
    transform: rotate(12.22deg);
    font-size: 0;
  }
}
